export default {
  props: {
    selectedWorkFlows: {
      type: Array,
      default () {
        return []
      }
    },
    item: {
      type: Object,
      default () {
        return {}
      }
    }
  },
  computed: {
    WorkFlowIDs () {
      return this.selectedWorkFlows.map((item) => {
        return item.WorkFlowID
      })
    },
    checked () {
      return this.WorkFlowIDs.includes(this.item.WorkFlowID)
    }
  }
}



